'use strict';

angular.module('videoHub')
  .controller('SponsorDetailController', function($scope, $routeParams, SponsorService, $location, AlertService, Restangular, DetailPageControllerConstants) {
    $scope.sponsor = {};
    $scope.hideDatetimeSelectionModal = true;

    $scope.getSponsor = function() {
      if (_.has($routeParams, 'sponsorId')) {
        return SponsorService.one($routeParams.sponsorId).get().then(
          function(data) {
            $scope.sponsor = data;
          },
          function(data) {
            console.log(data);
          }
        );
      } else {
        $scope.sponsor = {};
        Restangular.restangularizeElement(null, $scope.sponsor, 'sponsors');
      }
    };

    $scope.save = function() {
      if ($scope.sponsorDetailForm.$valid) {
        $scope.sponsor.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        AlertService.error(DetailPageControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    $scope.successCallback = function(newSponsor) {
      displaySaveSuccessfulAlert().then(function() {
        if (!_.has($routeParams, 'sponsorId')) {
          $location.path('cms/sponsors/' + newSponsor.id);
        }
      });
    };

    function displaySaveSuccessfulAlert() {
      return AlertService.success(DetailPageControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(DetailPageControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      AlertService.info('If you would like to delete a sponsor, please contact your friendly sysadmin.', true);
    };
    $scope.getSponsor();
  });
