'use strict';
/**
 * Apply as an attribute to an element, when element is clicked it will open a
 *  a date time selection modal. All functionality is dependent on dates being
 *  moment objects.
 */
angular.module('videoHub')
  .directive('deleteModal', function($modal) {
    return {
      link: function(scope, element) {
        var modalInstance = null;

        element.addClass('delete-modal-opener');

        element.on('click', function() {
          modalInstance = $modal
            .open({
              templateUrl: 'shared/delete-modal/delete-modal.html',
              controller: 'DeleteModalController',
              scope: scope
            });
          modalInstance.result
            .then(function(confirm) {
              if (confirm) {
                scope.confirmCallback();
              }
            });
        });
      },
      restrict: 'A',
      scope: {
        confirmCallback: '='
      }
    };
  });
