'use strict';

angular.module('videoHub')
  .directive('filterButtons', ['ListService', function (ListService) {
    return {
      controller: function ($scope) {

        $scope.params = ListService.getParams();

        /**
         * Set a filter value on {@link ListService} then update its content.
         *
         * @param {String} newFilterValue - New filter to add to {@link ListService}.
         * @returns {Promise} resolves based on {@link ListService.updateList} and the
         *  update callback passed into this directive.
         */
        $scope.setFilterAndUpdateList = function (newFilterValue) {

          ListService.setFilter(newFilterValue);
          $scope.params = ListService.getParams();

          return ListService.updateList().then(function () {
            if ($scope.updateCallback) {
              $scope.updateCallback();
            }
          });
        };

      },
      restrict: 'E',
      scope: {
        updateCallback: '&'
      },
      templateUrl: 'shared/filter-buttons/filter-buttons.html'
    };
  }]);
