'use strict';
angular.module('videoHub')
  .service('DropdownFilterService', function(ChannelService, SeriesService, SeasonService) {

    //There are places you can have thistakes in this service, so instead of using this, I use DropdownFilterService
    var DropdownFilterService = this;
    DropdownFilterService.data = {};
    DropdownFilterService.filter = {};

    DropdownFilterService.setChannel = function(newChannel) {
      if (newChannel) {
        DropdownFilterService.data.channel = newChannel;
        DropdownFilterService.updateFilter();
        DropdownFilterService.setSeries(null);
        DropdownFilterService.populateSeriesList();
      } else {
        DropdownFilterService.clear();
      }
    };

    DropdownFilterService.setSeries = function(newSeries) {
      if (newSeries) {
        DropdownFilterService.data.series = newSeries;
        DropdownFilterService.updateFilter();
        DropdownFilterService.populateSeasonList();
      } else {
        DropdownFilterService.data.series = null;
        DropdownFilterService.data.season = null;
      }
    };

    DropdownFilterService.setSeason = function(newSeason) {
      if (newSeason) {
        DropdownFilterService.data.season = newSeason;
        DropdownFilterService.season = newSeason;
      } else {
        DropdownFilterService.season = null;
      }
    };

    DropdownFilterService.setChannelList = function(channelList) {
      DropdownFilterService.data.channelList = channelList;
    };

    DropdownFilterService.clear = function() {
      DropdownFilterService.data.channel = null;
      DropdownFilterService.data.series = null;
      DropdownFilterService.data.season = null;
    };

    DropdownFilterService.updateFilter = function() {
      if (_.has(DropdownFilterService.data.channel, 'id')) {
        DropdownFilterService.filter.channelId = DropdownFilterService.data.channel.id;
      } else {
        DropdownFilterService.filter.channelId = null;
      }

      if (_.has(DropdownFilterService.data.series, 'id')) {
        DropdownFilterService.filter.seriesId = DropdownFilterService.data.series.id;
      } else {
        DropdownFilterService.filter.seriesId = null;
      }
    };

    DropdownFilterService.populateChannelList = function() {
      return ChannelService.getList({
        'page_size': 100
      }).then(DropdownFilterService.setChannelList);
    };

    DropdownFilterService.populateSeriesList = function() {
      return SeriesService.getList({
        'page_size': 100,
        'f': 'channel:' + DropdownFilterService.filter.channelId
      }).then(function(data) {
        DropdownFilterService.data.seriesList = data;
      });
    };

    DropdownFilterService.populateSeasonList = function() {
      return SeasonService.getList({
        'page_size': 100,
        'f': 'series:' + DropdownFilterService.filter.seriesId
      }).then(function(data) {
        DropdownFilterService.data.seasonList = data;
      });
    };
  });
