'use strict';
angular.module('videoHub')
  .factory('authInterceptor', ['$rootScope', '$q', '$location', '$injector', 'httpRequestBuffer', 'localStorageService', function($rootScope, $q, $location, $injector, httpRequestBuffer, localStorageService) {
    return {
      'request': function(config) {
        config.headers = config.headers || {};
        var token = localStorageService.get('authToken');
        var isBettyCropperRequest = _.has(config.headers, 'X-Betty-Api-Key');
        if (token) {
          if (!config.ignoreAuthorizationHeader && !isBettyCropperRequest) {
            config.headers.Authorization = 'JWT ' + token;
          }
        }
        return config;
      },
      'responseError': function(response) {
        if (response.config) {
          var ignoreAuthModule = response.config.ignoreAuthModule || response.config.headers.ignoreAuthModule;
          if (!ignoreAuthModule) {
            if (response.status === 403) {
              var deferred = $q.defer();
              httpRequestBuffer.append(response.config, deferred);
              var authService = $injector.get('authService');
              authService.refreshToken();
            }
          }
        }
        return $q.reject(response);
      }
    };
  }])
  .config(['$httpProvider', function($httpProvider) {
    $httpProvider.interceptors.push('authInterceptor');
  }])
  .service('authService', ['$http', '$rootScope', '$location', 'httpRequestBuffer', 'localStorageService', 'AlertService', 'CurrentUser',function($http, $rootScope, $location, httpRequestBuffer, localStorageService, AlertService, CurrentUser) {
    var _login = function(username, password) {
      return $http.post('/api/token/auth', {
          'username': username,
          'password': password
        })
        .success(function(response) {
          localStorageService.set('authToken', response.token);
          CurrentUser.setCurrentUser(username);
          $location.path('cms/');
        })
        .error(function() {
          AlertService.error('Username or password provided is incorrect.', true);
        });
    };
    var _refreshToken = function() {
      //TODO: put into constant
      var token = localStorageService.get('authToken');
      return $http.post('/api/token/refresh', {
          'token': token
        }, {
          'ignoreAuthModule': true
        }).success(function(response) {
          localStorageService.set('authToken', response.token);
          httpRequestBuffer.retryAll();
        })
        .error(function() {
          httpRequestBuffer.rejectAll();
          AlertService.error('You failed to authenticate. Redirecting to login.', true);
          //TODO: put into constant
          $location.path('cms/login');
        });
    };

    return {
      login: _login,
      refreshToken: _refreshToken
    };
  }])
  // Thanks to witoldsz/angular-http-auth
  .factory('httpRequestBuffer', ['$injector',
    function($injector) {
      var buffer = [];

      var $http;

      function _retryHttpRequest(config, deferred) {
        function successCallback(response) {
          deferred.resolve(response);
        }

        function errorCallback(response) {
          deferred.reject(response);
        }
        $http = $http || $injector.get('$http');
        config.headers.ignoreAuthModule = true;
        $http(config).then(successCallback, errorCallback);
      }

      return {
        append: function(config, deferred) {
          buffer.push({
            config: config,
            deferred: deferred
          });
        },
        rejectAll: function(reason) {
          if (reason) {
            _.each(buffer, function(request) {
              request.deferred.reject(reason);
            });
          }
          buffer = [];
        },
        retryAll: function() {
          _.each(buffer, function(request) {
            _retryHttpRequest(request.config, request.deferred);
          });
          buffer = [];
        }
      };
    }
  ]);
