'use strict';
angular.module('videoHub')
  .controller('DeleteModalController',
    function($scope, SponsorService, $modalInstance) {
      SponsorService.getList().then(function(data) {
        $scope.sponsors = data;
      });

      $scope.selectSponsor = function(sponsor) {
        $scope.sponsor = sponsor;
      };

      $scope.clearSponsor = function() {
        $scope.sponsor = null;
      };

      $scope.close = function() {
        $modalInstance.close(true);
      };
    });
