'use strict';

// ****** App Config ****** \\
angular.module('moment', []).constant('moment', window.moment);

angular.module('videoHub', [
    'ngCookies',
    'ngRoute',
    'ngAnimate',
    'ngMessages',
    'restangular',
    'highcharts-ng',
    'BettyCropper',
    'ui.bootstrap',
    'LocalStorageModule',
    'moment'
  ])
  .config(function($locationProvider, $httpProvider, $routeProvider, $sceDelegateProvider, RestangularProvider) {
    //TODO: Put this code into a more meaningful locaiton. Throwing all of this stuff into config is not proper
    //Add some stuff to the whitelist to prevent XSS blocking stuff
    $sceDelegateProvider.resourceUrlWhitelist(['self', 'http://assets.onionstatic.com/videoads/*', 'http://onionwebtech.s3.amazonaws.com/**', 'http://tv.theonion.com/**', 'http://v.theonion.com/**']);

    $locationProvider.html5Mode(true);

    //Set the base URL for the Restangular stuff
    RestangularProvider.setBaseUrl('/api/v0/');

    //We need a response interceptor for 'getList' becuase it contains additional metadata that is normally stripped out by restangular
    RestangularProvider.addResponseInterceptor(function(data, operation) {
      if (operation === 'getList') {
        var results = data.results;
        if (results) {
          results.meta = {
            count: data.count,
            prev: data.prev,
            next: data.next
          };
        }
        return results;
      }
      return data;
    });

    var isoDatesToMoments = function (obj) {
      if (_.has(obj, 'published') && obj.published) {
        obj.published = moment(obj.published);
      }
    };
    var momentsToIsoDates = function (obj) {
      if (_.has(obj, 'published') && moment.isMoment(obj.published)) {
        obj.published = obj.published.format();
      }
    };
    RestangularProvider.addResponseInterceptor(function(data) {
      if (_.has(data, 'results') && _.isArray(data.results)){
        _.each(data.results, isoDatesToMoments);
      } else {
        isoDatesToMoments(data);
      }
      return data;
    });
    RestangularProvider.addRequestInterceptor(function (element) {
      momentsToIsoDates(element);
      return element;
    });

    $routeProvider
      .when('/cms/', {
        templateUrl: 'components/videolist/videolist.html',
        controller: 'VideoListController',
        reloadOnSearch: false
      })
      .when('/cms/series/', {
        templateUrl: 'components/serieslist/serieslist.html',
        controller: 'SeriesListController',
        reloadOnSearch: false
      })
      .when('/cms/channels/', {
        templateUrl: 'components/channellist/channellist.html',
        controller: 'ChannelListController',
        reloadOnSearch: false
      })
      .when('/cms/sponsors/', {
        templateUrl: 'components/sponsorlist/sponsorlist.html',
        controller: 'SponsorListController',
        reloadOnSearch: false
      })
      .when('/cms/login/', {
        templateUrl: 'components/login/login.html',
        controller: 'LoginController'
      })
      .when('/cms/video/new/', {
        templateUrl: 'components/videodetail/videodetail.html',
        controller: 'VideoDetailController'
      })
      .when('/cms/video/:videoId/', {
        templateUrl: 'components/videodetail/videodetail.html',
        controller: 'VideoDetailController'
      })
      .when('/cms/channel/new/', {
        templateUrl: 'components/channeldetail/channeldetail.html',
        controller: 'ChannelDetailController'
      })
      .when('/cms/channel/:channelId/', {
        templateUrl: 'components/channeldetail/channeldetail.html',
        controller: 'ChannelDetailController'
      })
      .when('/cms/series/new/', {
        templateUrl: 'components/seriesdetail/seriesdetail.html',
        controller: 'SeriesDetailController'
      })
      .when('/cms/series/:seriesId/', {
        templateUrl: 'components/seriesdetail/seriesdetail.html',
        controller: 'SeriesDetailController'
      })
      .when('/cms/sponsors/new/', {
        templateUrl: 'components/sponsordetail/sponsordetail.html',
        controller: 'SponsorDetailController'
      })
      .when('/cms/sponsors/:sponsorId/', {
        templateUrl: 'components/sponsordetail/sponsordetail.html',
        controller: 'SponsorDetailController'
      })
      .otherwise({
        redirectTo: '/cms/'
      });
  })
  .constant('ValidQueryParamsForListPage', [
    'q', 'page', 'f', 'page_size', 'order_by'
  ])
.constant('DetailPageControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.',
    DELETE_SUCCESSFUL: 'Delete successful'
  });
