'use strict';
angular.module('videoHub')
  .controller('ChannelDetailController', function($scope, $routeParams, ChannelService, $location, AlertService, DetailPageControllerConstants, Restangular) {
    $scope.channel = {};

    $scope.getChannel = function() {
      if (_.has($routeParams, 'channelId')) {

        return ChannelService.one($routeParams.channelId).get().then(
          function(data) {
            $scope.channel = data;
            $scope.partner = $scope.channel.partner;
            if (!_.isUndefined(data.published)) {
              $scope.published = data.published;
            }
          },
          function(data) {
            console.log(data);
          }
        );
      } else {
        $scope.channel = {};
        Restangular.restangularizeElement(null, $scope.channel, 'channels');
      }
    };

    $scope.save = function() {
      if ($scope.channelDetailForm.$valid) {
        if (_.has($scope.partner, 'id')) {
          $scope.channel.partner = $scope.partner.id;
        } else {
          $scope.channel.partner = null;
        }

        if (_.isEmpty($scope.published)) {
          $scope.channel.published = null;
        } else {
          $scope.channel.published = $scope.published;
        }

        $scope.channel.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        AlertService.error(DetailPageControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    $scope.successCallback = function(channel) {
      displaySaveSuccessfulAlert().then(function() {
        if (!_.has($routeParams, 'channelId')) {
          $location.path('cms/channel/' + channel.id + '/');
        }
      });
    };

    function displaySaveSuccessfulAlert() {
      return AlertService.success(DetailPageControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(DetailPageControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      AlertService.info('If you would like to delete a channel, please contact your friendly sysadmin.', true);
    };

    $scope.getChannel();
  });
