'use strict';
/**
 * Apply as an attribute to an element, when element is clicked it will open a
 *  a date time selection modal. All functionality is dependent on dates being
 *  moment objects.
 */
angular.module('videoHub')
  .directive('sponsorSelector', function($modal) {
    return {
      link: function(scope, element) {
        var modalInstance = null;

        element.addClass('sponsor-selection-modal-opener');

        element.on('click', function() {
          modalInstance = $modal
            .open({
              templateUrl: 'shared/sponsor-selection-modal/sponsor-selection-modal.html',
              controller: 'SponsorSelectorModalController',
              scope: scope
            });
          modalInstance.result
            .then(function(newSponsor) {
              scope.sponsor = newSponsor;
              if (scope.sponsorSelectorModalOnClose) {
                scope.sponsorSelectorModalOnClose({
                  sponsor: newSponsor
                });
              }
            });
        });
      },
      restrict: 'A',
      require: '^ngModel',
      scope: {
        sponsor: '=ngModel',
        sponsorSelectorModalTitle: '@',
      }
    };
  });
