'use strict';

angular.module('videoHub')
  .directive('autocomplete', function($injector) {
    return {
      templateUrl: 'shared/autocomplete/autocomplete.html',
      replace: true,
      scope: {
        'model': '=',
        'service': '@service',
        'labelAttr': '@',
        'placeholderText': '@',
      },
      link: function(scope, element, attrs) {
        scope.isRequired = attrs.isRequired === 'true';
      },
      controller: function($scope) {
        //Inject service based on provided value
        var service = $injector.get($scope.service);

        $scope.doSearchAndUpdateCurrentModels = function() {
          if ($scope.modelSelected || $scope.showDropdown || _.isEmpty($scope.search) || $scope.search.length < 4) {
            $scope.currentModels = [];
            $scope.showDropdown = !_.isEmpty($scope.currentModels) && !$scope.modelSelected;
            return;
          } else {
            service.getList({
              'q': $scope.search
            }).then($scope.updateCurrentModelsAndDisplayDropdown);
          }
        };

        $scope.updateCurrentModelsAndDisplayDropdown = function(data) {
          if (!_.isUndefined(data)) {
            $scope.currentModels = data;
            $scope.showDropdown = !_.isEmpty($scope.currentModels) && !$scope.modelSelected;
          } else {
            $scope.currentModels = null;
            $scope.showDropdown = false;
          }
        };

        $scope.selectedElement = 0;
        $scope.controlSelectedElementWithKeyboard = function(event) {
          var keyCode = event.keyCode;
          if ($scope.currentModels) {
            var lastIndexOfCurrentModels = $scope.currentModels.length - 1;
            switch (keyCode) {
              case 27: // ESC
                $scope.disableDropdown();
                break;
              case 40: // DOWN
                $scope.selectedElement = $scope.selectedElement === lastIndexOfCurrentModels ?
                  0 :
                  $scope.selectedElement + 1;
                break;
              case 38: // UP
                $scope.selectedElement = $scope.selectedElement === 0 ?
                  lastIndexOfCurrentModels :
                  $scope.selectedElement - 1;
                break;
              case 13: // RETURN
                $scope.setModel($scope.currentModels[$scope.selectedElement]);
                $scope.selectedElement = 0;
                break;
              default:
                return;
            }
          }

        };

        $scope.disableDropdown = function() {
          $scope.showDropdown = false;
          $scope.selectedElement = 0;
          $scope.search = '';
          $scope.modelSelected = false;
        };

        $scope.setModel = function(model) {
          if (!_.isUndefined(unbindListener)) {
            unbindListener();
          }
          $scope.model = model;
          $scope.search = model[$scope.labelAttr];
          if ($scope.currentModels) {
            $scope.currentModels = null;
          }
          $scope.modelSelected = true;
          $scope.showDropdown = false;
          $scope.selectedElement = 0;
        };

        $scope.clear = function() {
          $scope.model = null;
          $scope.modelSelected = false;
          $scope.search = '';
        };

        var unbindListener = $scope.$watch('model', function(newValue) {
          if (_.has(newValue, 'name')) {
            $scope.setModel(newValue);
            unbindListener();
          }
        });
      }
    };
  });
