'use strict';

angular.module('videoHub')
  .directive('dropdownSelector', ['$injector', function($injector) {
    return {
      templateUrl: 'shared/dropdownselector/dropdownselector.html',
      replace: true,
      scope: {
        'ngModel': '=',
        'ngRequired': '@',
        'service': '@',
        'placeholderText': '@',
      },
      controller: function($scope) {
        var service = $injector.get($scope.service);
        $scope.populateCurrentItemsList = function() {
          service.getList({
            'page_size': 100
          }).then(function(results) {
            $scope.currentItems = results;
          });
        };

        $scope.populateCurrentItemsList();
      }
    };
  }]);
