'use strict';
angular.module('videoHub').directive('channelSelection', ['DropdownFilterService', function(DropdownFilterService) {
  return {
    templateUrl: 'components/videodetail/channelselection.html',
    transclude: true,
    link: function($scope) {
      $scope.data = DropdownFilterService.data;
      $scope.channelFieldChanged = function() {
        DropdownFilterService.setChannel($scope.data.channel);
      };
      $scope.seriesFieldChanged = function() {
        DropdownFilterService.setSeries($scope.data.series);
      };
      DropdownFilterService.populateChannelList();
    }
  };
}]);
