'use strict';
angular.module('videoHub')
  .controller('SeriesDetailController', function($scope, $routeParams, SeriesService, $location, AlertService, Restangular, DetailPageControllerConstants) {
    $scope.series = {};

    $scope.getSeries = function() {
      if (_.has($routeParams, 'seriesId')) {
        return SeriesService.one($routeParams.seriesId).get().then(
          function(data) {
            $scope.series = data;
            $scope.dropdownFields.channel = data.channel;
            $scope.sponsor = data.sponsor;
            if (!_.isUndefined(data.published)) {
              $scope.published = data.published;
            }
          },
          function(data) {
            console.log(data);
          }
        );
      } else {
        $scope.series = {};
        Restangular.restangularizeElement(null, $scope.series, 'series');
        $scope.sponsor = {};
      }
    };

    $scope.save = function() {
      if ($scope.seriesDetailForm.$valid) {
        if (_.has($scope.dropdownFields.channel, 'id')) {
          $scope.series.channel = $scope.dropdownFields.channel.id;
        } else {
          $scope.series.channel = null;
        }

        if (_.has($scope.sponsor, 'id')) {
          $scope.series.sponsor = $scope.sponsor.id;
        } else {
          $scope.series.sponsor = null;
        }

        if (_.isEmpty($scope.published)) {
          $scope.series.published = null;
        } else {
          $scope.series.published = $scope.published;
        }
        $scope.series.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        AlertService.error(DetailPageControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    $scope.successCallback = function(series) {
      displaySaveSuccessfulAlert().then(function() {
        if (!_.has($routeParams, 'seriesId')) {
          $location.path('cms/series/' + series.id);
        }
      });
    };

    function displaySaveSuccessfulAlert() {
      return AlertService.success(DetailPageControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(DetailPageControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      AlertService.info('If you would like to delete a series, please contact your friendly sysadmin.', true);
    };

    $scope.dropdownFields = {};
    $scope.getSeries();
  });
