'use strict';

/**
 * Apply as an attribute to an element, when element is clicked it will open a
 *  a date time selection modal. All functionality is dependent on dates being
 *  moment objects.
 */
angular.module('videoHub')
  .directive('dateTimeSelector', function($modal) {
    return {
      link: function (scope, element) {
        var modalInstance = null;

        element.addClass('datetime-selection-modal-opener');

        element.on('click', function () {
          modalInstance = $modal
            .open({
              templateUrl: 'shared/date-time-selector/date-time-selector-modal.html',
              controller: 'DateTimeSelectorModalController',
              scope: scope
            });
          modalInstance.result
            .then(function (newDate) {
              scope.modDateTime = newDate;
              if (scope.dateTimeSelectorModalOnClose) {
                scope.dateTimeSelectorModalOnClose({newDate: newDate});
              }
            });
        });
      },
      restrict: 'A',
      require: '^ngModel',
      scope: {
        modDateTime: '=ngModel',
        dateTimeSelectorModalTitle: '@',
        dateTimeSelectorModalOnClose: '&'
      }
    };
  });
