'use strict';
angular.module('videoHub')
  .controller('SponsorListController', function($scope, $location, $routeParams, $rootScope, ListService) {
    ListService.setService('SponsorService');

    var update = function () {
      $scope.list = ListService.data.list;
      $scope.totalItems = ListService.data.totalItems;
      $scope.params = ListService.getParams();
    };
    ListService.updateList().then(update);

    $scope.changeToEditPage = function(id) {
      $location.path('cms/sponsors/' + id + '/');
    };

    $scope.setOrderAndUpdateList = function(newOrderValue) {
      ListService.setOrder(newOrderValue);
      ListService.updateList().then(update);
    };

    $scope.clearSearchAndUpdateList = function() {
      ListService.clearSearch();
      ListService.updateList().then(update);
    };

    $scope.selectSearchBox = function(){
      $('#searchBox').focus();
    };

    $scope.pageChanged = function(newPageNumber) {
      ListService.setPage(newPageNumber);
      ListService.updateList().then(update);
    };

    $scope.setSearchAndUpdateList = function() {
      ListService.setSearch($scope.params.q);
      ListService.updateList().then(update);
    };
  });
