'use strict';

angular.module('videoHub')
  .directive('createContent', function () {
    return {
      restrict: 'E',
      templateUrl:  'shared/create-content/create-content.html',
      controller: function($scope, $location){
        $scope.setContentType = function(contentType){
          $scope.contentType = contentType;
        };

        $scope.goToContentTypesCreationPage = function(){
          $location.path('cms/' + $scope.contentType + '/new/');
        };
      }
    };
  });
